//  CSS
@import "./reset";

html {
  scrollbar-color: #000 transparent;
  scrollbar-width: thin;
}
body {
  font-family: $ff-main;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  color: $text-primary;
  background-color: var(--bg-app);
  @include macscrollbar;
}
body._lock {
  overflow: hidden;
  touch-action: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  // overflow: hidden;
}
.wrapper > .content {
  position: relative;
  z-index: 1;
  flex: 1 0 auto;
}
.main {
  padding-right: 8px;
  padding-left: 8px;
}

