.container {
  max-width: 1186px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  @media (max-width: 1023px) {
    max-width: calc(100% - 16px);
  }
}
input[type="search"]::-webkit-search-cancel-button {
  display: none;
  opacity: 0;
}


