.section-map ~ .callback-cards {
  padding-top: 8px;
}
.section-map__frame {
  width: 100%;
  height: 500px;
  border-radius: 12px;
  @media (max-width: 601px) {
    height: 590px;
  }
}


.flex {
  display: flex;
  width: 100%;
  justify-content: flex-start;
}
.flex--center {
  justify-content: center;
  align-items: center;
}
.flex--column {
  flex-direction: column;
}

._only-desktop {
  @media (max-width: 601px) {
    display: none !important;
  }
}
._only-mobile {
  @media (min-width: 602px) {
    display: none !important;
  }
}

.page__cart .page-heading {
    margin-bottom: 26px;
    @media (max-width: 601px) {
      margin-bottom: 31px;
    }
}
.page__cart .page-heading__title {
    margin-bottom: 0;
}