.header__snacks {
  position: relative;
  z-index: 2;
  // margin-left: -8px;
  overflow: hidden;
  transition: all 0.3s ease;
}
.header__snacks--show {
  padding-top: 80px;
  overflow: visible;
  @media (max-width: 1020px) {
    padding-top: 137px;
  }
}
.header-snack {
  position: absolute;
  top: 0;
  width: 100%;
  padding-top: 20px;
  padding-right: 18px;
  padding-bottom: 20px;
  padding-left: 28px;
  color: #fff;
  pointer-events: none;
  background: #5f6c75;
  opacity: 0;
  transition: all 0.3s ease;
  @media (max-width: 1020px) {
    min-height: 87px;
    // padding-left: 15px;
    margin-bottom: 20px;
    padding-left: 25px;
    padding-right: 25px ;
    padding-top: 12px;
    min-height: 134px;
    height: 100%;
  }
}

.header-snack .select__list--single::after {
  top: 7px;
  right: 10px;
  width: 24px;
  height: 24px;
}
.header-snack--visible {
  pointer-events: initial;
  opacity: 1;
}
.header-snack__container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  @media (max-width: 1020px) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 100%;
  }
}
.header-snack__title {
  width: 100%;
  max-width: 434px;
  margin-right: 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  @media (max-width: 1020px) {
    margin-bottom: 20px;
    margin-right: 0;
  }
}
.header-snack__content {
  width: 44%;
  @media (max-width: 1020px) {
    width: 100%;
  }
}
.header-snack__language  {
  justify-content: center;
  margin-bottom: 0;
  --chips-item-mb:0;
  @media (max-width: 1020px) {
    max-width: 100%;
    width: 100%  
  }
}
.header-snack__language  .chips__content {
  @media (max-width: 1020px) {
     display: grid;
    grid-template-columns: 1fr 1fr; 
    text-align: center;
  }
}
.header-snack__language  .header-snack__title {
  @media (max-width: 1020px) {
    max-width: 210px;
  }
}

.header-snack__close {
  --size: 40px;
  position: relative;
  width: var(--size);
  height: var(--size);
  margin-left: auto;
  @media (max-width: 1020px) {
    position: absolute;
    right: 13px;
    top: 4px;
  }
}
.header-snack__close::before,
.header-snack__close::after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 60%;
  height: 2px;
  content: "";
  background: #fff;
  transition: $tr;
}
.header-snack__close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.header-snack__close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.header-snack__select {
  max-width: 350px;
  --bg: transparent;
  border-radius: var(--brad);
  border: 1px solid #9FA7AC;
}
