.auth {
  opacity: 0;
  transform-origin: top;
  transform: scaleY(0);
  transition: all 0.3s ease, pointer-events 0s;
  position: absolute;
  width: 310px;
  right: 0;
  top: 40px;
  background: #5F6C75;
  border-radius: 12px;
  padding: 20px;
  // max-height: 557px;
  color: #fff;
}
.auth--visible {
  opacity: 1;
  transform: scaleY(1);
  pointer-events: initial;
}

.auth__form {
  margin-bottom: 16px;
}

.auth__methods {
  margin-bottom: 23px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.auth__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.auth__button-submit {
  border-radius: 6px;
  padding-left: 10px;
  padding-right: 10px;
}

.auth__fieldset {
 display: none;
}
.auth__fieldset--visible {
  display: block;
}

.auth__pages-message {
  z-index: 1;
  min-height: 1.2em;
  transition: all 0.3s ease;
  transform: var(--message-transform);
  transform-origin: top;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  background: #A1AEB3;
  border-radius: 6px;
  padding: 5px 10px 8px;
  display: inline-block;
  margin-bottom: 16px;
  margin-top: 8px; 
}

.auth__button-method-toggler {
  --button-color: #fff;
}

.auth__name {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  margin-bottom: 34px;
  display: block;
}
.auth__list-links {
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}
.auth__list-item {
  margin-bottom: 16px;
}
.auth__logout {
  padding-left: 0;
  padding-right: 0;
  font-size: 14px;
  font-weight: 400;
}
.auth__logout .button__icon {
  background-color: #fff;
  @include getmask("../img/common/logout.svg");
  width: 16px;
  height: 16px;
  min-width: 16px;
  margin-bottom: -1px;
}
